body {
    overflow-x: hidden !important;
    overflow: auto;
    margin:0;
    padding:0;
    background-color: #060607;
    font-size: 14px;
    width:100vw;

    /* background: rgb(2,0,36); */
}

body::-webkit-scrollbar {
    width: 1.2em;               /* width of the entire scrollbar */
}
  
body::-webkit-scrollbar-track {
    background: #1A1631;        /* color of the tracking area */
}
  
body::-webkit-scrollbar-thumb {
    border-radius: 4px;
    border:0.2em solid #1A1631;
    background: rgba(67, 61, 105, 0.5);
}

body::-webkit-scrollbar-thumb:hover {
    background: rgba(67, 61, 105, 0.8);
}

body::-webkit-scrollbar-thumb:active {
    background: rgba(67, 61, 105, 1);
}

a {
    color: inherit;
    text-decoration: none;
    pointer-events: auto;
}

ul {
    margin:0;
}

canvas {
    background-color: transparent !important;
}

.button {
    display: inline-flex;
    padding: 0.6em 0.9em;
    justify-content: center;
    align-items: center;
    gap: 0.6em;
    border-radius: 0;
    border: 1px solid #5346A5;
    background-color: transparent;
    color:#B6ABFF;
    font-family: Roboto;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    transition: 0.15s;
    pointer-events: auto;
}

.button img {
    width:1em;
}

.button:hover {
    border: 1px solid #5346A5;
    background-color: #5346A5;
    color:rgba(255, 255, 255, 0.87);
    transition: 0.15s;
}

#artboard {
    z-index:10;
    position:absolute;
    bottom:0;
    left:50vw;
    transform:translateX(-50%);
    pointer-events: none;
    opacity:0;
    position:fixed;
    /* height:50vw !important; */
}

@keyframes sizeChange {
    0%, 100% {
      width: 52px;
      height: 52px;
    }
    50% {
      width: 60px;
      height: 60px;
    }
}

.start-button-fade-in {
    opacity : 1 !important;
    transition: 0.2s;;
}

#start-button-container {
    height: 50px;
    width: 50px;
    left:50%;
    bottom:0;
    margin:0 0 3vh 0;
    transform:translate(-50%);
    position: fixed;
    z-index: 500;
    transition:0.1s;
    opacity: 0;
}

#start-button {
    border-radius: 50%;
    background-color: #4aff396c;
    height:50px;
    width: 50px;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    pointer-events: auto;
    animation: sizeChange 1.3s infinite;
    position: absolute;
}

#start-button div {
    border-radius: 50%;
    background-color: #49FF39;
    height:32px;
    width: 32px;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    position: absolute;
}

#loader {
    width:10vw;
    z-index: 200;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    display:flex;
    flex-direction: column;
    gap:0.5em;
    opacity:1;
}

.loader-fade-out {
    opacity:0 !important;
    transition:0.3s;
    width:0 !important;
}

#loading-bar-container {
    height:0.4em;
    width:100%;
    background-color: #1A1631;
    border-radius: 0.2em;
}

#loading-text {
    color: #5346A5;
    font-family: Montserrat;
    font-size: 2em;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    width:100%;
    text-align: center;
    opacity: 0.5;
}

#loading-bar {
    width:0%;
    height:100%;
    border-radius: 0.2em;
    background: linear-gradient(90deg, #6E59FC 0%, #A89BFF 102.8%);
    box-shadow: 0px 0px 12px 0px rgba(127, 111, 233, 0.50);
    /* box-shadow: 0 0 4px 1px #9082f0; */
}

/* main fade in classes for divs */

.div-fade-in {
    transform: translateY(50px);
    opacity:0 !important;
    transition-property: transform, opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-out;
}

.div-fade-out {
    /* transform: translateY(-50px); */
    opacity:0 !important;
    transition-property: transform, opacity;
    transition-duration: 0.4s;
    transition-timing-function: ease-out;
}

.artboard-fade-out-mobile {
    /* transform: translateY(-50px); */
    opacity:0 !important;
    transition-property: transform, opacity, scale;
    transition-duration: 0.4s;
    transition-timing-function: ease-out;
    scale:0.98;
    transform-origin: 0% 100%;
}

.artboard-fade-in-mobile {
    /* transform: translateY(-50px); */
    opacity:1 !important;
    transition-property: transform, opacity, scale;
    transition-duration: 0.4s;
    transition-timing-function: ease-out;
    transform-origin: 0% 100%;
    scale:1;
}

.div-in-view {
    transform: none;
    opacity: 1;
    transition-property: transform, opacity;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
}

