.project-title {
    color:rgba(255, 255, 255, 0.87);
    font-family: Montserrat;
    font-size: 2em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.project:nth-of-type(2n+1) .project-title {
    text-align: right;
}

.project-desc {
    color: rgba(255, 255, 255, 0.55);
    font-family: Roboto;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.project:nth-of-type(2n+1) .project-desc {
    text-align: right;
}

#project-container a {
    display:flex;
    gap: 0.56em;
    align-items: center;
    color: #B6ABFF;
    font-family: Roboto;
    font-size: 0.9em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    transition:gap 0.15s;
}

#project-container a img {
    width:1em;
}

.project-white-text {
    color: rgba(255, 255, 255, 0.87);
}

#project-container .project-corners {
    display:flex;
    flex-direction: column;
    height:100%;
    justify-content: space-between;
    position:absolute;
    right:0;
    pointer-events: none;
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

#project-container .project:nth-of-type(2n+1) .project-corners {
    left:0;
}

#project-container .project:nth-of-type(2n+1) .project-corners img {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

#project-container .project-corners img {
    width:1em;
}

.top-corner, .bottom-corner{
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

.project:nth-of-type(2n):hover .project-corners {
    margin-right:-5px;
}

.project:nth-of-type(2n+1):hover .project-corners {
    margin-left:-5px;
}

.project:nth-of-type(2n):hover .project-corners .top-corner{
    margin-top:-5px;
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

.project:nth-of-type(2n):hover .project-corners .bottom-corner{
    margin-bottom:-5px;
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

.project:nth-of-type(2n+1):hover .project-corners .top-corner{
    margin-top:-5px;
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

.project:nth-of-type(2n+1):hover .project-corners .bottom-corner{
    margin-bottom:-5px;
    transition:all 0.2s cubic-bezier(0.69, -0.49, 0.42, 1.4) 0s;
}

/* also check div */

#also-check {
    display:flex;
    justify-content: space-between;
    width:100%;
    margin:0 auto;
    position:relative;
}

.also-check-child {
    display: flex;
    flex-direction: column;
    gap:1.71em;
    width: 30em;
}

.also-check-child .card {
    display: flex;
    flex-direction: column;
    gap: 0.42em;
    width:100%;
    box-sizing: border-box;
}

.also-check-child .also-check-title {
    color: rgba(255, 255, 255, 0.87);
    font-family: Montserrat;
    font-size: 2em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.also-check-child .also-check-content {
    color:rgba(255, 255, 255, 0.55);
    font-family: Roboto;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.also-check-child img {
    border-radius: 1.6em;
}

/* footer */

#footer {
    text-align: center;
    color: rgba(255, 255, 255, 0.55);
    font-family: Montserrat;
    font-size: 1.14em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    height:1.7em;
    box-sizing: border-box;
}

/* form */

.projects-width:nth-of-type(3) {
    width: 30em;
    border-bottom:none;
    padding:0 0 4vh 0;
    box-sizing: border-box;
}

#contact-form {
    display:flex;
    flex-direction: column;
    gap: 1.42em;
}

#contact-form .form-row {
    display:flex;
    flex-direction: column;
    gap: 0.57em;
}

#contact-form .form-label {
    color: rgba(255, 255, 255, 0.55);
    font-family: Roboto;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

#contact-form .form-field {
    padding: 0.57em 0.6em;
    border: 1px solid rgba(255, 255, 255, 0.36);
    background:none;
    color:rgba(255, 255, 255, 0.87);
    font-size:1em;
    transition: border 0.075s;
    box-sizing: border-box;
    font-family: 'Roboto';
    border-radius: 0;
    -webkit-border-radius: 0;
}

#contact-form .form-field:hover {
    border: 1px solid rgba(255, 255, 255, 0.87);
}

#contact-form .form-field:focus {
    outline: none !important;
    border: 1px solid #B6ABFF;
}

#contact-form .form-text-area {
    /* height: 2.28em; */
    /* padding: 0.57em 0.85em; */
    border: 1px solid rgba(255, 255, 255, 0.36);
    background:none;
    resize:vertical;
    min-height:12em;
    color:rgba(255, 255, 255, 0.87);
    padding: 0.57em 0.6em;
    font-size:1em;
    transition: border 0.075s;
    font-family: 'Roboto';
}

#contact-form .form-text-area:hover {
    border: 1px solid rgba(255, 255, 255, 0.87);
}

#contact-form .form-text-area:focus {
    outline: none !important;
    border: 1px solid #B6ABFF;
}

#contact-form .form-buttons {
    display:flex;
    width:100%;
    justify-content: space-between;
    transition:0.15s;
}

#contact-form .form-buttons .button {
    transition:0.15s;
}

#contact-form .form-buttons .button:nth-of-type(2) {
    background: #5346A5;
    color: rgba(255, 255, 255, 0.87);
}

/* .form-buttons .button:nth-of-type(2):hover {
    display:flex;
    width:100%;
    justify-content: space-between;
} */

#contact-form .form-buttons .button:hover {
    border: 1px solid #5346A5;
    background-color: #5346A5;
    color:rgba(255, 255, 255, 0.87);
    transition: 0.15s;
}

#about-nav-anchor {
    position:absolute;
    width:10vw;
    height:100px;
    opacity:0;
    top:28vh;
}

.light-purple {
    color: #7D6DE2;
}

#back-to-top {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.56em;
    color: #B6ABFF;
    font-family: Roboto;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

#back-to-top img {
    transition:0.2s ease-in-out;
}

#back-to-top:hover img {
    transform:translateY(-0.2em);
    transition:0.1s ease-in-out;
}

#grainy-svg {
    width:100vw;
    height:100vh;
    position:fixed;
    top:0;
    z-index: 10;
    mix-blend-mode: normal;
    opacity:0.1; /* increase to 0.2 for the grainy effect */
    pointer-events: none;
}


.nav-right-panel-hidden {
    opacity:1;
    transform: translateX(0);
}

/* media queries ___________________________________________________________________________________________________________ */

@media (max-width:600px) {
    body {
        font-size:16px;
    }
    #artboard {
        position:fixed;
    }
    #backgrounds {
        position: fixed;
        top:0;
    }

    #loader {
        width:7em;
    }
    #loading-bar-container {
        height:0.2em;
        width:100%;
        background-color: #1A1631;
        border-radius: 0.2em;
    }
    #loading-text {
        font-size: 1.4em;
    }

    #top-nav {
        width:100%;
        padding:2.5em 2.5em;
        background: linear-gradient(180deg, #0A0217 0%, rgba(14, 3, 33, 0.00) 100%);
        align-items: center;
    }
    #top-nav-hamburger {
        display:block;
        width:28px;
        pointer-events: all;
    }
    #nav-right-panel {
        position:absolute;
        top:0;
        right:0;
        padding:2.6em 2.5em 0 0;
        align-items: flex-end;
        font-size: 1.1em;
        gap:3vh;
        height:100vh;
        width:15em;
        background: linear-gradient(270deg, #090714 0%, rgba(10, 7, 21, 0.87) 51%, rgba(10, 8, 21, 0.70) 75%, rgba(10, 8, 21, 0.00) 100%);
        transition:opacity 0.4s;

    }
    .nav-right-panel-hidden {
        opacity:0;
        transform:translateX(100em);
        transition:opacity 0.4s;
    }
    .nav-module:hover:not(.active) {
        border: none;
        color:#ffffff5c;
    }
    #nav-right-panel img {
        width:24px;
        display: block;
        pointer-events: all;
        z-index: 1000;
    }
    #nav-right-panel li:first-of-type {
        display:flex;
        flex-direction: column;
        align-items: flex-end;
        gap:2em;
    }
    .active {
        border:none !important;
    }
    .active:hover {
        border:none !important;
    }

    #website-logo {
        width:32px;
        margin:0;
    }
    .central-left-div {
        width:100%;
    }
    #central-div-1 .central-left-div {
        width:80%;
    }
    #central-div-1 {
        width:100%;
        height:80vh;
        padding:6em 2.5em 0;
        flex-direction: column;
        align-items: center;
    }
    #bg-banner-1 {
        flex-direction: column;
        align-items: center;
        width:100%;
        margin:0 2.5em;
        font-size:30vw;
        top:40vh;
        display:none;
    }
    #name-div img {
        width:100%;
        margin:0;
    }
    #central-div-1 .central-left-div div:nth-of-type(2) {
        font-size:1em;
        text-align:center;
        margin:1.2em 0 0 0;
    }
    #central-div-2 {
        width:100%;
        height:100vh;
        padding:2em 2.5em 2em;
        opacity:0;
    }
    #bg-banner-2 {
        /* flex-direction: column;
        align-items: center;
        width:50%;
        margin:0 auto;
        padding:0;
        font-size:26vw;
        top:40vh;
        text-align: center; */
        display:none;
    }
    .central-left-div .title img {
        width:100%;
        margin:0;
    }
    #central-div-2 .central-left-div {
        align-items: center;
        gap:3em;
    }
    #about-nav-anchor {
        top:0;
    }
    #work-exs {
        align-items: center;
    }
    .work-ex-selector {
        align-items: center;
        opacity:1;
    }
    .work-ex-selector div:first-of-type {
        font-size:1.2em;
        color: #b6abff;
    }
    .work-ex-selector div:nth-of-type(2) span:first-of-type {
        color: #ffffffde;
    }
    .work-ex-selector div:nth-of-type(2) img {
        color: #ffffffde;
    }
    .work-ex-selector div:nth-of-type(2) {
        font-size:1em;
    }
    .work-ex-selector div:nth-of-type(2) span:nth-of-type(2) {
        color: #ffffff8c;
    }
    .work-ex-active {
        box-shadow: none;
    }
    #central-div-3 {
        width:100%;
        height:100vh;
        padding:2em 2.5em 2em;
        margin:0;
    }
    #central-div-3 .central-left-div {
        align-items: center;
        gap:2em;
    }
    #central-div-3 .central-left-div title {
        width:100%;
    }
    #skills-div {
        width:100%;
        align-items: center;
    }
    #skills-div .skill-div {
        width:100%;
        align-items: center;
    }
    #skills-div .skill-div .skill-div-heading {
        font-size:1.2em;
    }
    #skills-div .skill-div .skill-div-content {
        align-items: center;
        justify-content: center;
        gap:0.5em 0.9em;
        font-size:1em;
    }
    #bg-banner-3 {
        display:none;
    }

    #project-container {
        width:100%;
        /* padding:2em 2.5em 2em; */
        margin:0;
        gap:3em;
    }
    #work-projects-svg {
        width:10em;
        margin:0 auto;
    }
    .project {
        flex-direction: column !important;
        gap:1.5em;
        margin:0;
    }
    .project-image {
        width:100vw;
        border-radius: 0;
        /* margin-left:-5em; */
    }
    .project-info {
        /* width:100%; */
        align-items: flex-start !important;
        gap:1.5em;
        padding:0 2.5em;
    }
    .projects-width {
        width:100%;
        gap:3em;
        border:none;
    }
    .project:nth-of-type(odd) .project-desc {
        text-align: left;
    }
    .project-title {
        font-size: 1.6em;
    }
    .project-desc {
